@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Gilroy";
  font-display: swap;
  src: url("../fonts/Gilroy-Bold.otf") format("ttf");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Gilroy";
  font-display: swap;
  src: url("../fonts/Gilroy-Regular.otf") format("ttf");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal__description {
  text-align: center;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.container-checkbox {
  width: 100%;
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  padding: 0 40px;
  background-color: #ebebeb;
}

.container-checkbox__title {
  margin: 0;
  padding: 15px 0;
}

.container-checkbox__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container-checkbox__item {
  position: relative;
}

.container-checkbox__item:not(:last-child) {
  margin-bottom: 15px;
}

.container-checkbox__checkbox {
  visibility: hidden;
}

.container-checkbox__control-checkbox {
  position: absolute;
  width: 24px;
  height: 24px;
  border-radius: 5px;
  background-color: #ffffff;
  left: 0;
  top: -1px;
  transition: 0.3s;
}
.container-checkbox__control-checkbox:hover {
  box-shadow: inset 0 0px 0px 2px #7859CF;
}
.container-checkbox__control-checkbox:focus {
  background: #e3daff;
  box-shadow: inset 0 0px 0px 3px white;
}

.container-checkbox__checkbox[type=checkbox]:checked + .container-checkbox__control-checkbox::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #7859CF;
  border-radius: 50%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.container-checkbox__description {
  margin-left: 15px;
  font-weight: 500;
}

.container-modal {
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  background-color: #000000;
}

.button-order {
  margin: 20px 20px;
  border: 2px solid #ffffff;
  border-radius: 30px;
  color: #ffffff;
  padding: 15px 40px;
  display: inline-flex;
  transition: 0.5s;
}
.button-order:hover {
  background-color: #ffffff;
  color: #000000;
}

.container-tabs {
  background-color: rgb(224, 224, 224);
  width: 100vw;
  height: 100vh;
}

.container-tabs__cards {
  list-style: none;
  margin: 0;
  padding: 0;
  display: none;
}

.container-tabs__cards--active {
  display: block;
}

.container-tabs__buttons {
  display: flex;
  justify-content: center;
  padding-top: 40px;
}

.container-tabs__button {
  background-color: #fff;
  border: none;
  padding: 15px 50px;
}

.container-tabs__button--active {
  background-color: rgb(108, 106, 255);
  color: #fff;
  animation: switchButton 0.3s forwards;
}

.container-tabs__button:first-child {
  border-bottom-left-radius: 8px;
  border-top-left-radius: 8px;
}

.container-tabs__button:last-child {
  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;
}

@keyframes switchButton {
  0% {
    background-color: #fff;
    color: #000;
  }
  100% {
    background-color: rgb(108, 106, 255);
    color: #fff;
  }
}
.container-tabs__card-logo {
  margin-right: 32px;
}

.container-tabs__card {
  background-color: #fff;
  margin: 30px auto;
  display: flex;
  justify-content: space-around;
  width: 500px;
  flex-direction: column;
  align-items: center;
  padding: 20px 40px 10px 15px;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  position: relative;
}

.container-tabs__cards--active {
  animation: showContent 0.6s forwards;
  position: relative;
}

@keyframes showContent {
  0% {
    bottom: -30px;
    opacity: 0;
  }
  100% {
    bottom: 0;
    opacity: 1;
  }
}
.container-tabs__card-logo-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 15px;
}

.container-tabs__card-wrapper {
  display: flex;
  width: 100%;
}

.container-tabs__cards-info,
.container-tabs__cards-transit,
.container-tabs__cards-transfer {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 33%;
}

.container-tabs__card-direction,
.container-tabs__card-transit,
.container-tabs__card-transfer {
  margin-bottom: 15px;
}

.container-tabs__card-transit-title,
.container-tabs__card-direction-time,
.container-tabs__card-direction-transfer,
.container-tabs__card-transfer-title,
.container-tabs__card-direction-title {
  white-space: nowrap;
  margin: 0;
}

.container-tabs__card-direction-title,
.container-tabs__card-transit-title,
.container-tabs__card-transfer-title {
  color: gray;
}

.container-tabs__card-direction-transfer,
.container-tabs__card-direction-time {
  font-weight: 500;
}

.container-tabs__card-cost {
  font-size: 26px;
  color: rgb(108, 106, 255);
}

@media (max-width: 767px) {
  .container-tabs__buttons {
    padding: 20px 8px;
  }
  .container-tabs__button {
    padding: 10px 20px;
  }
  .container-tabs__cards-wrapper {
    padding: 0 20px;
  }
  .container-tabs__card-logo {
    margin: 0;
  }
  .container-tabs__card {
    width: 100%;
  }
  .container-tabs__card-wrapper {
    flex-wrap: wrap;
  }
  .container-tabs__cards-info,
.container-tabs__cards-transit,
.container-tabs__cards-transfer {
    width: 140px;
  }
}
.container-context {
  font-family: "Gilroy", Helvetica, sans-serif;
}

.container-context__wrapper {
  display: flex;
  position: relative;
  justify-content: center;
}

.container-context__wrapper-content {
  position: relative;
}

.container-context__wrapper-content::before {
  content: "";
  position: absolute;
  background: #FF9900;
  filter: blur(150px);
  width: 377px;
  height: 377px;
  z-index: -1;
  left: 15%;
}

.container-context__wrapper-content::after {
  content: "";
  position: absolute;
  background: #FF9900;
  filter: blur(150px);
  width: 377px;
  height: 377px;
  z-index: -1;
  right: 10%;
  top: 30%;
}

.container-context__logo-wrapper {
  position: relative;
  width: 320px;
  height: 371px;
}

.container-context__logo {
  position: absolute;
  z-index: -1;
}

.container-context__logo--left {
  top: 180px;
  animation: imageAnimationLeft 1s forwards;
  animation-delay: 1s;
  left: 0px;
  opacity: 0;
}

.container-context__logo--right {
  top: 50px;
  animation: imageAnimationRight 1s forwards;
  animation-delay: 1s;
  right: 0px;
  opacity: 0;
}

@keyframes imageAnimationLeft {
  0% {
    left: 0px;
    opacity: 0;
  }
  100% {
    left: 35px;
    opacity: 0.6;
  }
}
@keyframes imageAnimationRight {
  0% {
    right: 0px;
    opacity: 0;
  }
  100% {
    right: 35px;
    opacity: 0.6;
  }
}
.container-context__button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-top: 21px;
}

.container-context__title {
  font-weight: 700;
  font-size: 80px;
  line-height: 99px;
  position: relative;
  text-align: center;
  animation: textAnimation 1s forwards;
  margin-top: 100px;
  margin-bottom: 21px;
}

.container-context__description {
  font-weight: 400;
  font-size: 24px;
  line-height: 150%;
  text-align: center;
  position: relative;
  animation: descAnimation 1s forwards;
  animation-delay: 0.6s;
  opacity: 0;
}

@keyframes textAnimation {
  0% {
    opacity: 0;
    top: -70px;
  }
  100% {
    opacity: 1;
    top: 0px;
  }
}
@keyframes descAnimation {
  0% {
    opacity: 0;
    top: -70px;
  }
  100% {
    opacity: 1;
    top: 0px;
  }
}
.container-context__button {
  cursor: pointer;
  position: relative;
  animation: textAnimation 1s forwards;
}

.container-context__button-order {
  background: #FFEE00;
  border-radius: 100px;
  width: 200px;
  height: 50px;
  border: none;
  margin: 0 auto;
  display: block;
  font-weight: 600;
  position: relative;
  animation: textAnimation 1s forwards;
  animation-delay: 1s;
  opacity: 0;
}

.container-context__discont-wrapper {
  position: absolute;
  right: 0;
}

.container-context__discont-wrapper {
  position: absolute;
  right: -140px;
  bottom: -35px;
}

.container-context__discont-title {
  float: left;
  background: #000;
  color: #fff;
  width: 202px;
  height: 57px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 10px;
  border-radius: 15px;
  position: absolute;
  z-index: 1;
  left: 5px;
  animation: discontTitle 1s forwards;
  opacity: 0;
}

@keyframes discontTitle {
  0% {
    opacity: 0;
    bottom: 35px;
    transform: rotate(0deg);
  }
  100% {
    opacity: 1;
    bottom: 55px;
    transform: rotate(-15deg);
  }
}
.container-context__discont-title::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background: #FFFFFF;
  transform: rotate(-15deg);
  border-radius: 50%;
  left: 27px;
}

.container-context__discont-description {
  background: white;
  color: #000;
  font-weight: 600;
  border-radius: 15px;
  font-size: 18px;
  line-height: 150%;
  padding: 14px 10px;
  width: 370px;
  height: 84px;
  margin: 0;
  text-align: end;
  opacity: 0;
  animation: descAnimation 1s forwards;
  position: relative;
}

@keyframes descAnimation {
  0% {
    opacity: 0;
    bottom: -50px;
  }
  100% {
    opacity: 1;
    bottom: 0px;
  }
}
@media (min-width: 1680px) {
  @keyframes imageAnimationLeft {
    0% {
      left: 0px;
      opacity: 0;
    }
    100% {
      left: 35px;
      opacity: 1;
    }
  }
  @keyframes imageAnimationRight {
    0% {
      right: 0px;
      opacity: 0;
    }
    100% {
      right: 35px;
      opacity: 1;
    }
  }
}
@media (max-width: 1440px) {
  .container-context__title {
    font-size: 60px;
    line-height: 70px;
  }
  .container-context__discont-wrapper {
    right: -200px;
  }
  .container-context__description {
    font-size: 20px;
  }
}
@media (max-width: 1279px) {
  .container-context__discont-wrapper {
    position: static;
    margin: 0;
    margin-top: 70px;
  }
