.container-tabs {
  background-color: rgb(224, 224, 224);
  width: 100vw;
  height: 100vh;
}
.container-tabs__cards {
  list-style: none;
  margin: 0;
  padding: 0;
  display: none;
}
.container-tabs__cards--active {
  display: block;
}
.container-tabs__buttons {
  display: flex;
  justify-content: center;
  padding-top: 40px;
}
.container-tabs__button {
  background-color: #fff;
  border: none;
  padding: 15px 50px;

}
.container-tabs__button--active {
  background-color: rgb(108, 106, 255);
  color: #fff;
  animation: switchButton 0.3s forwards;
}
.container-tabs__button:first-child {
  border-bottom-left-radius: 8px;
  border-top-left-radius: 8px;
}
.container-tabs__button:last-child {
  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;
}
@keyframes switchButton {
  0% {
    background-color: #fff;
    color: #000;
  }
  100% {
    background-color: rgb(108, 106, 255);
    color: #fff;
  }
}
.container-tabs__card-logo {
  margin-right: 32px;
}
.container-tabs__card {
  background-color: #fff;
  margin: 30px auto;
  display: flex;
  justify-content: space-around;
  width: 500px;
  flex-direction: column;
  align-items: center;
  padding: 20px 40px 10px 15px;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  position: relative;
}
.container-tabs__cards--active {
  animation: showContent 0.6s forwards;
  position: relative;
}
@keyframes showContent {
  0% {
    bottom: -30px;
    opacity: 0;
  }
  100% {
    bottom: 0;
    opacity: 1;
  }
}
.container-tabs__card-logo-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 15px;
}
.container-tabs__card-wrapper {
  display: flex;
  width: 100%;

}
.container-tabs__cards-info,
.container-tabs__cards-transit,
.container-tabs__cards-transfer {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 33%;
}
.container-tabs__card-direction,
.container-tabs__card-transit,
.container-tabs__card-transfer {
  margin-bottom: 15px;


}
.container-tabs__card-transit-title,
.container-tabs__card-direction-time,
.container-tabs__card-direction-transfer,
.container-tabs__card-transfer-title,
.container-tabs__card-direction-title {
  white-space: nowrap;
  margin: 0;

}
.container-tabs__card-direction-title,
.container-tabs__card-transit-title,
.container-tabs__card-transfer-title {
  color: gray;
}
.container-tabs__card-direction-transfer,
.container-tabs__card-direction-time {
  font-weight: 500;
}
.container-tabs__card-cost {
  font-size: 26px;
  color: rgb(108, 106, 255);
}
@media(max-width:767px) {
  .container-tabs__buttons {
    padding: 20px 8px;
  }
  .container-tabs__button  {
    padding: 10px 20px;
  }
  .container-tabs__cards-wrapper {
    padding: 0 20px;
  }
  .container-tabs__card-logo {
    margin: 0;
  }
  .container-tabs__card {
    width: 100%;

  }
  .container-tabs__card-wrapper {
    flex-wrap: wrap;
  }
  .container-tabs__cards-info,
  .container-tabs__cards-transit,
  .container-tabs__cards-transfer {
    width: 140px;
  }
}

