.container-checkbox {
  width: 100%;
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  padding: 0 40px;
  background-color: #ebebeb;
}
.container-checkbox__title {
  margin: 0;
  padding: 15px 0;
}
.container-checkbox__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.container-checkbox__item {
  position: relative;

}
.container-checkbox__item:not(:last-child) {
  margin-bottom: 15px;
}
.container-checkbox__checkbox {
  visibility: hidden;
}

.container-checkbox__control-checkbox {
  position: absolute;
  width: 24px;
  height: 24px;
  border-radius: 5px;
  background-color: #ffffff;
  left: 0;
  top: -1px;
  transition: 0.3s;

  &:hover {
      box-shadow: inset 0 0px 0px 2px #7859CF;
  }
  &:focus {
      background: #e3daff;
      box-shadow: inset 0 0px 0px 3px white;
  }
}
.container-checkbox__checkbox[type="checkbox"]:checked
+ .container-checkbox__control-checkbox::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #7859CF;
  border-radius: 50%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

}

.container-checkbox__description {
  margin-left: 15px;
  font-weight: 500;
}
