.container-modal {
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  background-color: #000000;
}
.button-order {
  margin: 20px 20px;
  border: 2px solid #ffffff;
  border-radius: 30px;
  color: #ffffff;
  padding:  15px 40px;
  display: inline-flex;
  transition: 0.5s;
  &:hover {
    background-color: #ffffff;
    color:#000000;
  }
}
