
.container-context {
  font-family: "Gilroy",  Helvetica, sans-serif;


}
.container-context__wrapper {
  display: flex;
  position: relative;

  justify-content: center;
}
.container-context__wrapper-content {
  position: relative;

}
.container-context__wrapper-content::before {
  content: "";
  position: absolute;
  background: #FF9900;
  filter: blur(150px);
  width: 377px;
  height: 377px;
  z-index: -1;
  left: 15%;
}
.container-context__wrapper-content::after {
  content: "";
  position: absolute;
  background: #FF9900;
  filter: blur(150px);
  width: 377px;
  height: 377px;
  z-index: -1;
  right: 10%;
  top: 30%;
}
.container-context__logo-wrapper {
  position: relative;
  width: 320px;
  height: 371px;
}
.container-context__logo {
  position: absolute;
  z-index: -1;
}
.container-context__logo--left {
  top: 180px;
  animation: imageAnimationLeft 1s forwards;
  animation-delay: 1s;
  left: 0px;
  opacity: 0;
}
.container-context__logo--right {
  top: 50px;
  animation: imageAnimationRight 1s forwards;
  animation-delay: 1s;
  right: 0px;
  opacity: 0;
}
@keyframes imageAnimationLeft {
  0% {
    left: 0px;
    opacity: 0;
  }

  100% {
    left: 35px;
    opacity: 0.6;
  }
}
@keyframes imageAnimationRight {
  0% {
    right: 0px;
    opacity: 0;
  }

  100% {
    right: 35px;
    opacity: 0.6;
  }
}
.container-context__button {
  background-color: rgba($color: #000000, $alpha: 0);
  border: none;
  margin-top: 21px;
}
.container-context__title {
  font-weight: 700;
  font-size: 80px;
  line-height: 99px;
  position: relative;
  text-align: center;
  animation: textAnimation 1s forwards;
  margin-top: 100px;
  margin-bottom: 21px;
}

.container-context__description {
  font-weight: 400;
  font-size: 24px;
  line-height: 150%;
  text-align: center;
  position: relative;
  animation: descAnimation 1s forwards;
  animation-delay: 0.6s;
  opacity: 0;
}
@keyframes textAnimation {
  0% {
    opacity: 0;
    top: -70px;
  }
  100% {
    opacity: 1;
    top: 0px;
  }
}
@keyframes descAnimation {
  0% {
    opacity: 0;
    top: -70px;
  }
  100% {
    opacity: 1;
    top: 0px;
  }
}
.container-context__button {
  cursor: pointer;
  position: relative;
  animation: textAnimation 1s forwards;

}
.container-context__button-order {
  background: #FFEE00;
  border-radius: 100px;
  width: 200px;
  height: 50px;
  border: none;
  margin: 0 auto;
  display: block;
  font-weight: 600;
  position: relative;
  animation: textAnimation 1s forwards;
  animation-delay: 1s;
  opacity: 0;
}
.container-context__discont-wrapper {
  position: absolute;
  right: 0;
}
.container-context__discont-wrapper {
  position: absolute;
  right: -140px;
  bottom: -35px;

}
.container-context__discont-title {
  float: left;
  background: #000;
  color: #fff;
  width: 202px;
  height: 57px;

  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 10px;
  border-radius: 15px;
  position: absolute;
  z-index: 1;
  left: 5px;
  animation: discontTitle 1s forwards;
  opacity: 0;
}
@keyframes discontTitle {
  0% {
    opacity: 0;
    bottom: 35px;
    transform: rotate(0deg);
  }
  100% {
    opacity: 1;
    bottom: 55px;
    transform: rotate(-15deg);
  }
}
.container-context__discont-title::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background: #FFFFFF;
  transform: rotate(-15deg);
  border-radius: 50%;
  left: 27px;
}
.container-context__discont-description {
  background: #ffff;
  color: #000;
  font-weight: 600;
  border-radius: 15px;
  font-size: 18px;
  line-height: 150%;
  padding: 14px 10px;
  width: 370px;
  height: 84px;
  margin: 0;
  text-align: end;
  opacity: 0;
  animation: descAnimation 1s forwards;
  position: relative;
}
@keyframes descAnimation {
  0% {
    opacity: 0;
    bottom: -50px;
  }
  100% {
    opacity: 1;
    bottom: 0px;
  }
}
@media (min-width: 1680px) {
  @keyframes imageAnimationLeft {
    0% {
      left: 0px;
      opacity: 0;
    }

    100% {
      left: 35px;
      opacity: 1;
    }
  }
  @keyframes imageAnimationRight {
    0% {
      right: 0px;
      opacity: 0;
    }

    100% {
      right: 35px;
      opacity: 1;
    }
  }
}
@media (max-width: 1440px) {
  .container-context__title {
    font-size: 60px;
    line-height: 70px;
  }
  .container-context__discont-wrapper {
    right: -200px;
  }
  .container-context__description {
    font-size: 20px;
  }
}
@include vp-1279 {
  .container-context__discont-wrapper {
    position: static;
    margin: 0;
    margin-top: 70px;

  }
  .container-context__discont-title {
    left: auto;
    right: 160px;
  }
  .container-context__discont-description {
    margin-left: auto;
  }
}
@media (max-width: 852px) {
  .container-context__title {
    font-size: 55px;
    line-height: 70px;
    margin-top: 70px;
  }
}
@include vp-767 {
  .container-context__title {
    font-size: 32px;
    line-height: 42px;
    margin-top: 80px;
  }
  .container-context__description {
    font-size: 18px;

  }
  .container-context__logo {
    width: 170px;
    height: 221px;
  }
  .container-context__button {
    position: fixed;
    left: 0;
    top: 0;
    margin: 0;
  }
  .container-context__button-image {
    width: 75px;
    height: 85px;
  }
  .container-context__discont-wrapper {
    top: -10px;
    transform: scale(0.8);
  }
  .container-context__wrapper-content {
    width: 100%;
  }
}
@media (max-width: 488px) {
  .container-context__discont-wrapper {
    position: relative;
    right: 0;
  }
  .container-context__title {
    font-size: 28px;
    line-height: 38px;
  }
  .container-context__description {
    font-size: 16px;
    width: auto;
  }
  .container-context__wrapper-content::before {
    width: 200px;
    height: 200px;
    z-index: -1;
    filter: blur(80px);
  }
  .container-context__wrapper-content::after {
    width: 200px;
    height: 200px;
    z-index: -1;
    filter: blur(80px);
  }
  @keyframes imageAnimationLeft {
    0% {
      left: 0px;
      opacity: 0;
    }

    100% {
      left: 0px;
      opacity: 0.6;
    }
  }
  @keyframes imageAnimationRight {
    0% {
      right: 0px;
      opacity: 0;
    }

    100% {
      right: 0px;
      opacity: 0.6;
    }
  }
  .container-context__discont-wrapper {
    top: -10px;
    transform: scale(0.7);
  }
  .container-context__discont-title {
    left: 0;
  }
}
